@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap);
:root {
  --primary-color: #4e95d7;
  --primary-hover: #3e79b1;
  --primary-text-dark: #464646;
  --primary-text-light: #828282;
  --background-light: #f8f8f8;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  font-size: 16px;
  font-family: "Inter", sans-serif;
  background-color: #f8f8f8;
  background-color: var(--background-light);
}

#root {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card {
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.1);
  display: block;
}

.main-container {
  width: 100%;
  max-width: 600px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.other-details-list {
  list-style-type: none;
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 0.5rem;
}

.other-details-list li {
  color: var(--primary-text-dark);
  font-size: 12px;
  font-weight: 600;
}

.other-details-list li span {
  color: var(--primary-text-light);
}

.cordinate-content .other-details-list {
  margin-top: 0;
}

.card-content {
  padding: 1rem;
}

@media (max-width: 600px) {
  #root {
    display: block;
    padding: 1rem;
  }
  .main-container {
    grid-template-columns: 1fr;
  }
}

label {
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  margin-bottom: 0.5rem;
  display: block;
  color: var(--primary-text-dark);
}

.form-card {
  grid-column: 1/ -1;
  padding: 2rem;
}

.form-row {
  display: grid;
  align-items: center;
  justify-content: flex-start;
  grid-template-columns: 1fr auto;
  grid-gap: 16px;
}

.input {
  padding: 10px 1rem;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 3px;
}

.form-btn {
  padding: 10px 1rem;
  background-color: var(--primary-color);
  color: #fff;
  border: 0;
  border-radius: 3px;
  cursor: pointer;
  min-width: 140px;
  text-align: center;
  transition: 0.2s ease-in-out;
  position: relative;
}

.form-btn:disabled {
  color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-btn:disabled::after {
  content: "";
  width: 15px;
  height: 15px;
  border: 2px solid #fff;
  border-top-color: transparent;
  animation-name: rotate;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  border-radius: 50px;
  position: absolute;
}

.form-btn:hover {
  background-color: var(--primary-hover);
  transition: 0.2s ease-in-out;
}

@media (max-width: 480px) {
  .form-row {
    grid-template-columns: 1fr;
  }
}

.card-header-wrapper {
  padding: 1rem;
  border-bottom: 1px solid #eaeaea;
}
.card-header {
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  color: var(--primary-color);
}

.loader-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: grid;
  place-content: center center;
  width: 60px;
}

.loader {
  width: 20px;
  height: 20px;
  border: 3px solid var(--primary-color);
  border-radius: 50px;
  border: top;
  border-top-color: transparent;
  display: block;
  animation-name: rotate;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes rotate {
  to {
    transform: rotateZ(360deg);
  }
}

.weather-row {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.weather-img {
  margin-right: 1rem;
}

.weather-img img {
  width: 60px;
  height: 60px;
  object-fit: contain;
}

.temp {
  font-size: 40px;
  font-weight: 400;
  color: var(--primary-color);
  position: relative;
  margin-right: 1.2rem;
}

.temp::after {
  content: "";
  position: absolute;
  top: 0;
  right: -15px;
  width: 12px;
  height: 12px;
  border-radius: 50px;
  border: 3px solid var(--primary-color);
}

.metric {
  font-size: 40px;
  font-weight: 400;
  color: var(--primary-text-dark);
}

.location {
  color: var(--primary-text-dark);
  font-size: 1rem;
}

.cordinates-card .other-details-list {
  margin-top: 0;
}

.cordinates-card {
  margin-bottom: 1rem;
}

.wind-card .other-details-list {
  margin-top: 0;
}

.wind-card {
  grid-column: 2 / auto;
  grid-row: 3 / auto;
}

.error-container {
  padding: 0.5rem 1rem;
  background-color: #ff0537;
  grid-column: 1/-1;
  border-radius: 3px;
}

.error-container h2 {
  font-size: 14px;
  font-weight: 600;
  color: #fff;
}

